

.backdrop {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;

    background: rgba(0, 0, 0, 0.2);
    opacity: 1;
    transition: opacity var(--effect-time) var(--effect-function);
    z-index: 5;
}

.backdrop--is-hidden {
    opacity: 0;
    pointer-events: none;
}

.backdrop--is-hidden2 {
    opacity: 0;
    pointer-events: none;
}

.backdrop--is-hidden .modal-window {
    transform: translate(-50%, -50%) scale(1.3);
}

.modal-window {
max-height: calc(100vh - 210px);
overflow: auto;


    position: absolute;
    left: 50%;
    top: 50%;
    padding: 40px;
    width: 1200px;
    background: var(--color-white);
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
        0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    transform: translate(-50%, -50%) scale(1);
    transition: transform var(--effect-time) var(--effect-function);

    // @media screen and (min-width: 1200px) {
    //     width: 528px;
    // }
}



.modal-window__close {
    position: absolute;
    top: 8px;
    right: 8px;

    border: 1px solid rgba(0, 0, 0, 0.1);

    width: 30px;
    height: 30px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--color-white);
    transition: background-color, color, box-shadow var(--effect-time) var(--effect-function);
    cursor: pointer;

    &:hover .modal-window__icon,
    &:focus .modal-window__icon {
        background-color: var(--color-white);
        fill: var(--color-accent);
        transition: background-color, color, box-shadow var(--effect-time) var(--effect-function);
    }
}

.modal-window__icon {
    @extend %site-animation;

    fill: black;
}